.testimonial-container {
  height: auto;
  margin: 0 auto;
  overflow: hidden;
  padding-bottom: 3rem;
  padding-top: 3rem;
  width: 100%;
}

.section-title {
  font-size: 1.5rem;
  margin: 0 auto;
  padding-bottom: 2rem;
  width: 75%;
}

.cards {
  align-items: center;
  display: flex;
  gap: 2rem;
  height: 80vh;
  justify-content: center;
  margin: 0 auto;
  width: 95%;
}

.testimonial {
  align-items: center;
  background-color: rgb(226, 228, 230);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  height: 80%;
  justify-content: center;
  padding-top: 1rem;
  position: relative;
  transition: 0.3s ease;
  width: 23%;
}

.quote-mark-left {
  bottom: 0;
  left: 11%;
  position: absolute;
  right: 0;
  top: 3%;
  z-index: 0;
}

.quote-mark-right {
  bottom: 0;
  left: 75%;
  position: absolute;
  right: 0;
  top: 44%;
}

.testimonial p {
  color: var(--secondary-title-colour);
  font-size: 1rem;
  font-style: italic;
  font-weight: 600;
  text-align: center;
  width: 70%;
  z-index: 1;
}

.testimonial img {
  padding-top: 2rem;
}

.user-name {
  color: var(--primary-title-colour);
  font-weight: bold;
  padding-top: 0.5rem;
}

.user-occupation {
  columns: var(--primary-title-colour);
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.middle-testimonial-image {
  height: auto;
  width: 35%;
}

.active-testimonial {
  background: var(--primary-title-colour);
  box-shadow: 3px 3px 5px 1px rgb(0 0 0 / 20%);
  color: var(--primary-link-colour);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  transform: scale(1.15, 1.15);
  transition: 0.3s ease-in;
}

.active-testimonial p {
  color: var(--primary-link-colour);
  width: 80%;
}

.active-testimonial .user-name {
  color: var(--primary-link-colour);
}

.active-testimonial .quote-mark-left, .active-testimonial .quote-mark-right {
  opacity: 0.4;
}

.button-circles {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding-top: 1.75rem;
  width: 80%;
}

.button-circles p {
  border-radius: 50%;
  cursor: pointer;
  height: 20px;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  width: 20px;
}

.inactive-button {
  background: #C5C9CD;
  transition: 0.3s ease;
}

.active-button {
  background:var(--primary-title-colour);
  transform: scale(1.3, 1.3);
  transition: 0.3s ease;
}

@media only screen and (max-width: 1075px) {
  .testimonial {
    border-radius: 10px;
    height: auto;
    padding-top: 1rem;
    position: relative;
    width: 40%;
  }

  .testimonial-container {
    width: 100%;
  }

  .cards {
    height: auto;
  }

  .section-title {
    padding-bottom: 5rem;
  }

  .button-circles {
    padding-top: 2.75rem;
  }
}

@media only screen and (max-width: 786px) {
  .button-circles {
    display: none;
  }

  .testimonial-container {
    height: auto;
    width: 100%;
  }

  .cards {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    height: 100%;
    margin: 3rem auto;
  }

  .active-testimonial {
    background: #E2E4E6;
    box-shadow: 3px 3px 5px 1px rgb(0 0 0 / 0%);
    color: black;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    transform: scale(1, 1);
    transition: 0.3s ease-in;
  }

  .active-testimonial .middle-testimonial-image {
    height: auto;
    width: 90px;
  }

  .testimonial {
    display: flex;
    height: auto;
    padding-top: 2rem;
    position: relative;
    width: 80%;
  }

  .testimonial p {
    color: var(--primary-link-colour);
    width: 70%;
  }

  .testimonial .user-name {
    color: var(--primary-title-colour);
  }

  .testimonial .user-occupation {
    padding-bottom: 1rem;
  }

  .testimonial p {
    color: var(--secondary-title-colour);
  }

  .testimonial:hover, .testimonial:active {
    background: var(--primary-title-colour);
    box-shadow: 3px 3px 5px 1px rgb(0 0 0 / 20%);
    color: var(--primary-link-colour);
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    transform: scale(1.15, 1.15);
    transition: 0.3s ease-in;
  }

  .testimonial:hover p, .testimonial:active p{
    color: var(--primary-link-colour);
  }

  .testimonial:hover .user-name, .testimonial:active .username {
    color: var(--primary-link-colour);
  }

  .quote-mark-left {
    top: 8%;
  }

  .quote-mark-right {
    top: 30%;
  }

  .testimonial:hover .quote-mark-left, .testimonial:hover .quote-mark-right,
  .testimonial:active .quote-mark-left, .testimonial:active .quote-mark-right {
    opacity: 0.4;
  }

  .section-title {
    padding-bottom: 1rem;
  }
}

@media only screen and (max-width: 540px) {
  .testimonial {
    display: flex;
    height: 70vh;
    padding-top: 1rem;
    position: relative;
    width: 80%;
  }
}
