.services-container {
  background-color: rgb(237, 239, 241);
  padding: 5rem 3rem 5rem 3rem;
}

.flex-container {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  width: 80%;
}

#left-side {
  width: 40%;
}

#left-side h2 {
  font-size: 2.3rem;
}

#title-top {
  color: var(--primary-title-colour);
  font-weight: 600;
}

#title-bottom {
  color: var(--secondary-title-colour);
  font-weight: 600;
}

#left-side p {
  color: var(--secondary-title-colour);
  font-size: 1.1rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
  width: 90%;
}

#right-side {
  display: grid;
  gap: 2rem;
  grid-template-areas: "brand marketing"
                       "development marketing";
  grid-template-columns: repeat(2, 1fr);

  grid-template-rows: repeat(2, 1fr);
  width: 45%;
}

.brand-strategy {
  grid-area: brand
};

.development {
  grid-area: development;
}

.section-marketing {
  grid-area: marketing;
}

.section-links {
  padding-top: 1.5rem;
}

.section-links h4 {
  color: var(--primary-title-colour);
  text-transform: uppercase
}

.section-links ul li {
  color: var(--secondary-title-colour);
  font-weight: 600;
  padding-top: 1rem;
}

.section-links li a {
  color: var(--secondary-title-colour);
  font-weight: 600;
}

.arrow-container {
  align-items: center;
  background: var(--secondary-title-colour);
  border-radius: 50%;
  color: rgb(255, 255, 255);
  display: flex;
  display: none;
  height: 23px;
  justify-content: center;
  text-align: center;
  transition: 0.3s ease;
  width: 23px;
}

.arrow {
  color: white;
}

.section-links li a:link {
  color: var(--secondary-title-colour);
}

.section-links li a:visited {
  color: var(--secondary-title-colour);
}

.section-links li a:hover {
  color: var(--secondary-title-colour);
}

.section-links li:hover .arrow-container {
  animation: arrow-slide 0.3s ease;
  display: inline-block;
  margin-left: 0.5rem;
}

.selected .arrow-container {
  animation: arrow-slide 0.3s ease;
  display: inline-block;
  margin-left: 0.5rem;
}

@keyframes arrow-slide {
  0%,
  100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(5px, 0);
  }
}

@media only screen and (max-width: 1200px) {
  .flex-container {
    width: 100%;
  }

  .section-marketing {
    margin-left: 2rem;
  }

}

@media only screen and (max-width: 904px) {
  .flex-container {
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }

  .section-marketing {
    margin-left: 0rem;
  }

  #left-side {
    margin-bottom: 4rem;
    width: 95%;
  }

  #right-side {
  width: 95%;
  }
}

@media only screen and (max-width: 540px) {
  #left-side {
    margin-bottom: 4rem;
    text-align: center;
    width: 100%;
  }

  #left-side p {
    width: 100%;
  }

  #right-side {
  width: 90%;
  }

  #right-side {
    display: grid;
    gap: 2rem;
    grid-template-areas: "brand"
                         "development"
                         "marketing";
    grid-template-columns: 1fr;

    grid-template-rows: 1fr;
    text-align: center;
  }
}
