.heading-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  max-width: 50vw;
}

.description-container {
  padding-top: 4rem;
  width: 50%;
}

.description-container h5 {
  color: rgb(192, 52, 94);
  margin-bottom: 2rem;
  opacity: 0.75;
}

.description-container h2 {
  font-size: 2.3rem
}

#title-one {
  color: var(--primary-title-colour);
  font-weight: 600;
}

#title-two {
  color: var(--secondary-title-colour);
  font-weight: 600;
}

.description-container p {
  color: var(--secondary-title-colour);
  font-size: 1.1rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.image-container {
  bottom: 0;
  height: 100%;
  left: 50%;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.heading-picture {
  background: no-repeat;
  background-size: cover;
  height: 100%;
  position: relative;
  width: 100%;
}

@media(max-width: 1024px) {
  .heading-container {
    max-width: 100vw;
  }

  .description-container {
    width: 50%;
  }

  #title-two {
    color: rgb(46, 58, 66);
  }

  .description-container p {
    color: rgb(54, 59, 65);
  }

  .image-container {
    bottom: 0;
    height: 100%;
    left: 0;
    opacity: 0.60;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -3;
  }
}

@media(max-width: 786px) {
  .description-container {
    width: 85%;
  }
}

@media(max-width: 578px) {
  .description-container h5 {
    color: rgb(192, 52, 94);
    font-size: 0.7rem;
    margin-bottom: 2rem;
    opacity: 0.75;
  }

  .description-container h2 {
    font-size: 1.8rem
  }
}
