.projects-container {
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 0rem auto;
  padding-bottom: 5rem;
  padding-top: 5rem;
  width: 75%;
}

.title-section {
  padding-bottom: 3rem;
}

.title-section h2 {
  font-size: 2.3rem;
  font-weight: bold;
}

.tabs {
  padding-bottom: 3rem;
}

.category-button {
  background: none;
  border: none;
  border-bottom: 1px solid black;
  color: var(--primary-title-colour);
  cursor: pointer;
  display: inline-block;
  height: 30px;
  margin: 0;
  margin-top: 1rem;
  transition: 0.3s ease;
  width: 130px;
}

.category-button::after {
  border-bottom: 3px solid #C0345E;
  content: '';
  display: block;
  height: 12px;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.category-button:hover {
  font-weight: bold;
}

.category-button:hover::after {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}

.active-tab {
  border-bottom: 3px solid #C0345E;
  font-weight: bold;
}

.gallery {
  display: grid;
  gap: 1.25rem;
  grid-template-areas: 'project01 project02 project03'
                       'project04 project04 project05';
  width: 100%;
}

.display-item {
  height: 100%;
  width: 100%;
}

#project01 {
  grid-area: project01;
}

#project03 {
  grid-area: project03;
}

#project04 {
  grid-area: project04;
  width: 100%;
}

#project05 {
  grid-area: project05;
}

.newspaper-card {
  background: linear-gradient(rgb(218, 206, 206) 1%, black);
  border: none;
  border-radius: 0.6rem;
  grid-area: project02;
  position: relative;
  z-index: 0;
}

.newspaper-card img {
  backface-visibility: hidden;
  opacity: 1;
  transition: 0.5s ease;
}

.newspaper-card .card-info {
  align-items: flex-start;
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  left: 50%;
  opacity: 0;
  padding-left: 3rem;
  position: absolute;
  top: 70%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease;
  width: 100%;
}

.newspaper-card .card-info h3 {
  font-size: 1.1rem;
  padding-bottom: 1rem;
}

#paragraph-bottom {
  margin-bottom: 1.5rem;
}

.newspaper-card .card-info p {
  font-size: 0.8rem;
  font-weight: normal;
}

.newspaper-card .card-info li {
  border-bottom: 2px solid var(--primary-link-colour);
}

.newspaper-card .card-info li a {
  color: var(--primary-link-colour);
  font-size: 0.9rem;
  padding-bottom: 0.2rem;
  position: relative;
}

.card-info li a::before {
  border-bottom: 2px solid var(--primary-link-colour);
  content: '';
  height: 100%;
  opacity: 0;
  position: absolute;
  transform: scaleY(2);
  transition: 0.3s;
  width: 100%;
}

.card-info li a:hover::before {
  opacity: 1;
  transform: scaleY(1);
}

.newspaper-card:hover img {
  cursor: pointer;
  opacity: 0.4;
}

.newspaper-card:hover .card-info {
  cursor: pointer;
  opacity: 1;
}

.navigation-buttons {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-bottom: 4rem;
  padding-top: 4rem;
}

.navigation-buttons button {
  background: var(--primary-title-colour);
  border: none;
  cursor: pointer;
  height: 40px;
  transition: 0.2s ease;
  width: 40px;
}

.navigation-buttons button:hover {
  transform: scale(1.15, 1.15);
  transition: 0.2s ease;
}

#button-prev {
  background: #50647380;
  margin-right: 1.25rem;
}

.navigation-buttons li {
  color: var(--primary-title-colour);
  font-weight: 600;
  transition: 0.5s;
}

.navigation-buttons li a {
  color: var(--primary-title-colour);
  position: relative;
}

.navigation-buttons li a::before {
  border-bottom: 3px solid #C0345E;
  content: '';
  height: 100%;
  opacity: 0;
  position: absolute;
  transform: scaleY(2);
  transition: 0.3s;
  width: 100%;
}

.navigation-buttons li a:hover::before {
  opacity: 1;
  transform: scaleY(1);
}

@media only screen and (max-width: 1074px) {
  .newspaper-card .card-info {
    align-items: flex-start;
    height: 80%;
    justify-content: center;
    padding-left: 0rem;
    top: 50%;
    width: 90%;
  }

  .newspaper-card .card-info h3 {
    font-size: 0.8rem;
  }

  .newspaper-card .card-info p {
    font-size: 0.6rem;
  }

  .newspaper-card .card-info li a {
    font-size: 0.7rem;
  }
}

@media only screen and (max-width: 736px) {
  .newspaper-card .card-info {
    align-items: center;
    height: 90%;
    justify-content: center;
    padding-left: 0rem;
    text-align: center;
    width: 90%;
  }

  .newspaper-card .card-info p {
    font-size: 0.8rem;
    font-weight: normal;
  }

  .newspaper-card .card-info li {
    padding-bottom: 0rem;
  }

  .newspaper-card .card-info h3 {
    font-size: 0.5rem;
  }

  .newspaper-card .card-info p {
    font-size: 0.4rem;
  }

  #paragraph-bottom {
    margin-bottom: 0.5rem;
  }

  .newspaper-card .card-info li a {
    font-size: 0.5rem;
    padding-bottom: 0rem;

  }
}

@media only screen and (max-width: 540px) {
  .projects-container {
    justify-content: center;
    text-align: center;
    width: 90%;
  }

  .gallery {
    grid-template-areas: 'project01 project02' 'project03 project05' 'project04 project04';
  }
}
