.about-container {
  align-items: center;
  background: rgb(237, 239, 241);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 8rem;
  padding-top: 8rem;
}

.masonry-gallery {
  display: grid;
  grid-template-areas: 'office1 office3 office2'
                       'office1 office4 office2';
  grid-template-columns: 2fr 1fr 1fr;
  width: 90%;
}

#office-01 {
  grid-area: office1;
  justify-self: end;
  width: 80%;
}

#office-02 {
  grid-area: office2;
  height: auto;
  width: 80%;
}

#office-03 {
  grid-area: office3;
  height: auto;
  width: 100%;
}

#office-04 {
  grid-area: office4;
  height: auto;
  justify-self: end;
  width: 75%;
}

.extra-links {
  display: flex;
  margin-top: -2.5rem;
  width: 70%;
}

.extra-links li {
  border-bottom: 3px solid rgb(192, 52, 94);
  margin-right: 3rem;
}

@media only screen and (max-width: 768px) {
  .extra-links li a {
    font-size: 0.6rem;
  }
}

@media only screen and (max-width: 540px) {
  .masonry-gallery {
    display: grid;
    grid-template-areas: 'office1'
                         'office3'
                         'office2'
                         'office4';
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    width: 90%;
  }

  #office-01 {
    grid-area: office1;
    width: 100%;
  }

  #office-02 {
    grid-area: office2;
    height: auto;
    width: 100%;
  }

  #office-03 {
    grid-area: office3;
    height: auto;
    width: 100%;
  }

  #office-04 {
    grid-area: office4;
    height: auto;
    justify-self: normal;
    width: 100%;
  }

  .extra-links {
    margin-top: 0rem;
  }

  .extra-links li {
    margin-right: 1rem;
  }
}
