.editable-button li {
  color: var(--primary-title-colour);
  cursor: pointer;
  font-weight: 600;
  transition: 0.5s;
}

.editable-button li a {
  color: var(--primary-title-colour);
  position: relative;
}

.editable-button li a::before {
  border-bottom: 3px solid rgb(191, 51, 93);
  content: '';
  height: 100%;
  opacity: 0;
  position: absolute;
  transform: scaleY(2);
  transition: 0.3s;
  width: 100%;
}

.editable-button li a:hover::before {
  opacity: 1;
  transform: scaleY(1);
}
