.footer-container {
  background: rgb(237, 239, 241);
  height: 100%;
  padding-bottom: 2rem;
  padding-top: 5rem;
  width: 100%;
}

.main-info {
  display: flex;
  gap: 1rem;
  height: 100%;
  margin: 0 auto;
  width: 85%;
}

.footer-title-section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 6%;
  width: 30%;
}

.footer-title-section h2 {
  font-size: 2.3rem;
}

.links-section {
  display: flex;
  flex-wrap: wrap;
  gap: 7rem;
  height: 100%;
  justify-content: flex-end;
  width: 70%;
}

.links-section h3 {
  color: var(--primary-title-colour);
  font-weight: 600;
  padding-bottom: 1rem;
  text-transform: uppercase;
}

.links-section h4 {
  color: var(--secondary-title-colour);
  font-weight: 600;
}

.links-section p {
  color: var(--secondary-title-colour);
  padding-bottom: 2rem;
}

.links-section li {
  color: var(--secondary-title-colour);
  font-weight: 600;
  padding-bottom: 1.3rem;
}

.links-section li a {
  color: var(--secondary-title-colour);
  padding-bottom: 1rem;
}

.arrow-container {
  align-items: center;
  background: var(--secondary-title-colour);
  border-radius: 50%;
  color: rgb(255, 255, 255);
  display: flex;
  display: none;
  height: 23px;
  justify-content: center;
  text-align: center;
  transition: 0.3s ease;
  width: 23px;
}

.links-section li:hover .arrow-container {
  animation: arrow-slide 0.3s ease;
  display: inline-block;
  margin-left: 0.5rem;
}

@keyframes arrow-slide {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(5px, 0);
  }
}

.social-section {
  align-items: center;
  color: rgba(80, 100, 115, 0.5);
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 3rem;
  width: 75%;
}

.social-section p {
  font-size: 0.9rem;
  font-weight: 600;
}

.social-section a {
  color: rgb(158, 169, 177);
}

.social-icons {
  padding-right: 1.5rem;
  transition: 0.2s ease;
}

.social-icons:hover {
  transform: scale(1.15, 1.15);
  transition: 0.2s ease;
}

@media only screen and (max-width: 1080px) {
  .main-info {
    display: flex;
    gap: 0rem;
    margin: 0 auto;
    width: 95%;
  }

  .footer-title-section {
    margin-left: 0%;
  }

  .footer-title-section h2 {
    font-size: 2rem;
  }

  .social-section {
    width: 95%;
  }
}

@media only screen and (max-width: 934px) {
  .main-info {
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    width: 95%;
  }

  .footer-title-section {
    margin-left: 0%;
  }

  .footer-title-section h2 {
    font-size: 2.5rem;
  }

  .footer-title-section {
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 0%;
    text-align: start;
    width: 90%;
  }

  .footer-link {
    padding-top: 1.5rem;
  }

  .links-section {
    display: flex;
    flex-wrap: wrap;
    gap: 7rem;
    justify-content: center;
    width: 100%;
  }

  .links-section h3 {
    color: var(--primary-title-colour);
    font-weight: 600;
    padding-bottom: 1rem;
    text-transform: uppercase;
  }

  .social-section {
    width: 90%;
  }
}

@media only screen and (max-width: 670px) {
  .footer-title-section h2 {
    font-size: 2rem;
  }

  .footer-title-section {
    align-items: center;
    flex-direction: column;
    justify-content: center;
    margin-left: 0%;
    text-align: center;
    width: 95%;
  }

  .links-section {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    width: 100%;
  }

  .links-section h3 {
    color: var(--primary-title-colour);
    font-weight: 600;
    padding-bottom: 1rem;
    text-transform: uppercase;
  }

  .social-section {
    flex-direction: column;
    gap: 2rem;
  }

  .social-section p {
    text-align: center;
  }
}
