.nav-container {
  height: 7rem;
  left: 50%;
  position: fixed;
  text-align: center;
  top: 0;
  transform: translateX(-50%);
  transition: top 0.5s;
  width: 100vw;
  z-index: 2000;
}

.navbar-hidden {
  top: -50%;
}

.navbar-visible {
  top: 0;
}

.navbar {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0rem auto;
  margin-top: 1rem;
  width: 85%;
}

.nav-container img {
  height: 81px;
  width: auto;
}

.nav-elements {
  display: flex;
  gap: 1.5rem;
  margin-right: 1.5rem;
}

.nav-elements li {
  cursor: pointer;
  list-style: none;
  margin-right: 1rem;
  transition: 0.25s ease;
};

.nav-elements li a {
  color: var(--primary-link-colour);
  font-weight: 600;
}

.nav-elements li a:link {
  color: var(--primary-link-colour);
}

.nav-elements li a:visited {
  color: var(--primary-link-colour);
}

.nav-elements li:hover {
  transform: scale(1.15, 1.15);
  transition: 0.25s ease;
}

.nav-elements li a:hover {
  border-bottom: 2px solid var(--primary-link-colour);
  color: var(--primary-link-colour);
}

.nav-elements li a:active {
  border-bottom: 2px solid var(--primary-link-colour);
}

.default-link {
  border-bottom: 2px solid var(--primary-link-colour);
}

.hamburger {
  background: none;
  border: none;
  color: var(--primary-link-colour);
  display: none;
  transition: 0.3s ease-in-out;
}

@media(max-width: 1024px) {
  .nav-elements {
    margin-right: 0;
  }
}

@media (max-width: 768px) {
  .hamburger {
    display: block;
    overflow: hidden;
    z-index: 1000;
  }

  .nav-elements {
    align-items: center;
    background: rgba(0, 0, 0, 0.9);
    bottom: 0;
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    left: -130%;
    margin-right: 0;
    min-height: 100vh;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 0;
    transition: 0.5s;
    width: 100vw;
    z-index: 200;
  }

  .nav-elements li {
    font-size: 2rem;
    margin-right: 0rem;
  };

  .nav-elements li a {
    color: var(--primary-link-colour);
    font-weight: 600;
  }

  .nav-elements.active {
    left: 50%;
    touch-action: none;
    transform: translateX(-50%);
  }
}
